export default {
  name: 's-tabs',
  props: {
    // class
    customClass: {
      type: String,
      default: ''
    },
    // v-model双向绑定
    value: {
      type: Number,
      default: 0
    },
    color: {
      type: String,
      default: '#333'
    },
    activeColor: {
      type: String,
      default: '#406BDC'
    },
    // 标签栏高度 rpx
    height: {
      type: Number,
      default: 80
    },
    // 自适应宽度还是百分比等分
    navPerView: {
      type: [Number, String],
      default: 'auto'
    },
    // 内容部分是否动画切换
    effect: {
      type: Boolean,
      default: false
    },
    // 内容部分动画切换时间
    duration: {
      type: Number,
      default: 0.3
    },
    // 是否显示底部条
    line: {
      type: Boolean,
      default: true
    },
    // 底部条颜色
    lineColor: {
      type: String,
      default: '#406BDC'
    },
    // 底部条高度 rpx
    lineHeight: {
      type: Number,
      default: 4
    },
    // 底部条宽度相对于标签宽度比例
    lineScale: {
      type: Number,
      default: 0.6
    }
  },
  data () {
    return {
      scrollLeft: 0,
      lineWidth: 0,
      lineLeft: 0,
      navList: []
    };
  },
  computed: {
    navWidth () {
      const perView = parseInt(this.navPerView);
      return isNaN(perView) ? 'auto' : 100 / perView + '%';
    },
    transform () {
      return `transform: translate3d(${-100 * this.value}%, 0, 0);`;
    },
    transition () {
      return this.effect ? `transition-duration: ${this.duration}s;` : '';
    }
  },
  provide () {
    return {
      $tabs: this
    };
  },
  watch: {
    value (index) {
      this.refreshLine();
      this.$emit('change', index);
    }
  },
  methods: {
    navClick (index) {
      if (index !== this.value) {
        this.$emit('input', index);
      }
    },
    refreshLine () {
      if (!this.line) return;
      this.$nextTick(() => {
        const query = () => uni.createSelectorQuery().in(this);
        query().select('.s-tabs-nav-wrap').boundingClientRect().exec(([viewElem]) => {
          const viewWidth = viewElem.width;
          let offsetLeft = 0;
          let contentWdith = 0;
          let curNavWidth = 0;
          query().selectAll('.s-tab-nav').boundingClientRect().exec(([list]) => {
            list.forEach((item, index) => {
              if (index <= this.value) {
                curNavWidth = item.width;
                offsetLeft += item.width;
              }
              contentWdith += item.width;
            });
            offsetLeft -= curNavWidth;
            this.scrollLeft = Math.min(Math.max(contentWdith - viewWidth, 0), Math.max(0, offsetLeft - (viewWidth - curNavWidth) / 2));
            this.lineWidth = curNavWidth * this.lineScale;
            this.lineLeft = offsetLeft + (curNavWidth - this.lineWidth) / 2;
          });
        });
      });
    }
  },
  mounted () {
    this.refreshLine();
  }
};
